<script>
import itemsData from '../assets/json/data.json'
export default {
  data() {
    return {
      activeMenu: 0,
      navList: itemsData,
    }
  },
  computed: {
    defaultAvatar() {
      return 'this.src="' + require('../assets/images/default.png') + '"'
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll, true)
  },
  methods: {
    handleScroll() {
      let scrollY = window.scrollY
      let len = this.navList.length
      for (let i = 0; i < len; i++) {
        if (scrollY >= this.$refs[`list${i}`].offsetTop) {
          this.activeMenu = i
        }
      }
    },
    jump(i) {
      this.activeMenu = i
      document.documentElement.scrollTop = this.$refs[`list${i}`].offsetTop
    },
  },
}
</script>

